.lecture-card {
  padding: 0 0 24px;
}

.lecture-card .dd {
  position: relative;
  top: 4px;
}

.lecture-card .card-actions {
  padding: 16px 24px;
}

@media (max-width: 600px) {
  .lecture-card .card-actions {
    padding: 16px;
  }
}

.lecture-card .card-actions .action-btn {
  -ms-flex-pack: end;
      justify-content: flex-end;
}

.lecture-card .lecture-status {
  margin: 0;
  padding: 0;
  list-style: none;
  -ms-flex: 1 1;
      flex: 1 1;
}

.lecture-card .lecture-status li {
  float: left;
  height: 24px;
  line-height: 24px;
  padding-right: 24px;
}

.lecture-card .lecture-status li em {
  font-size: 14px;
  color: #e64a19;
  font-style: normal;
}

p {
  padding: 0;
  margin: 0;
}


